/* Стили секции lesson */

.lesson {
  padding-bottom: 150px;
}

.lesson .container {
  align-items: flex-start;
  font-family: "Open Sans", Arial, sans-serif;
  width: 900px;
  line-height: 1.33;
  animation: bounceInRight 1s;
}

.lessons .container {
  animation: bounceInLeft 1s;
}

.lesson__content {
  padding: 100px 0 0;
  flex-direction: column;
  display: flex;
  align-items: flex-start;
  font-weight: 300;
  line-height: 24px;
}

.lesson__chapter {
  line-height: 1;
  color: #222222;
  text-transform: uppercase;
  letter-spacing: 3px;
  font-weight: 600;
  font-size: 18px;
}

.lesson__chapter-name {
  font-size: 36px;
  line-height: 54px;
  color: #222222;
  font-weight: 800;
  position: relative;
  margin-bottom: 100px;
}

.lesson__chapter-name::after {
  content: "";
  position: absolute;
  display: block;
  width: 70%;
  height: 5px;
  bottom: -20px;
  left: 0;
  background-color: #e8c762;
}

.lesson__video {
  display: flex;
  align-items: center;
  align-self: center;
  padding-bottom: 85px;
}

.lesson__video iframe {
  border-radius: 15px;
}

.lesson__materials {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}

.lesson__material {
  padding: 15px 0;
  font-size: 18px;
  line-height: 24px;
  color: #222222;
}

.lesson__material:first-child {
  padding-top: 50px;
}

.lesson__item {
  padding: 10px 0;
  font-size: 18px;
}

.lesson__item:first-child {
  padding-top: 20px;
}

i {
  font-family: Verdana, sans-serif;
  font-weight: 700;
}

.lesson__title {
  font-size: 36px;
  line-height: 54px;
  color: #222222;
  font-weight: 800;
  position: relative;
  margin: 50px 0;
}

.lesson__title::after {
  content: "";
  position: absolute;
  display: block;
  width: 70%;
  height: 5px;
  bottom: -20px;
  left: 0;
  background-color: #e8c762;
}

.lesson__img {
  padding: 20px 0;
}

.no_padding {
  padding: 0;
}

.next-lesson {
  background-image: url(../img/header2.jpg);
  background-repeat: no-repeat;
  background-position: center center;
  background-attachment: fixed;
  background-size: cover;
  align-self: center;
  padding: 150px 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.end {
  position: relative;
}

.end:before {
  content: "Следующий урок в разработке";
  display: block;
