// ЦВЕТА


// РАЗМЕРЫ

  $desktop: 1200px;
  $laptop: 992px;
  $tablets: 768px;
  $phones: 480px;


// ШРИФТЫ

$mainFont: "Open Sans", Arial, sans-serif;

// Функции

@mixin circle($width, $color) {
    width: $width;
    height: $width;
    border-radius: $width/2;
    background-color: $color;
  }
  
  //responsive
  @function rem($px) {
    @return $px / 16px+0rem;
  }
  
  @mixin tablets {
    @media screen and (max-width: $tablets) {
      @content;
    }
  }
  
  @mixin phones {
    @media screen and (max-width: $phones) {
      @content;
    }
  }